.hidden{
  display: none;
}

.new-note-layout { 
  border: 1px solid rgba(0,0,0,.125);
  padding: 0 !important;
}

label {
  width: 150px !important;
}

.label-item {
  width: 120px;
}

.new-note-checkbox {
  position: relative;
  margin-top: 1rem;
  margin-left: 0;
}

.note-checkbox{
  position: relative;
  margin-left: 0;
}

.big-checkbox{
  width: 30px;
  height: 30px;
}

h5{
  margin: 0 !important;
}

.btn-warning {
  background-color: #ff8507;
  border-color: #ff8507;
}

.border-light {
  border-color: #d2d2d2 !important;
}

.login-box {
  height: 500px;
  background-color: #fcfcfc;
}

.btn-warning:hover {
  background-color: #cf8c48;
  border-color: #cf8c48;
}

.label-item {
  margin-right: 1em;
}

textarea.stepsTaken-note, textarea.issue-note {
  color: #212529;
}

.clipboard-button, .note-edit-button {
  cursor: pointer;
}

.clipboard-button:hover {
  font-size: 1.5em;
  transition: font-size 0.25s ease-out;
}

.note-edit-button:hover {
  font-size: 1.2em;
  transition: font-size 0.25s ease-out;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
